import router from '@/router'
import { getToken } from '@/utils/auth'
import store from './store'

router.beforeEach(async (to, from, next) => {
  // document.title = to.meta.title
  const hasToken = getToken()
  // 是否有token，有直接跳转
  const { path, meta } = to
  if (hasToken || !meta.requireAuth) {
    if (path === '/login') {
      next()
    } else {
      const hasRoutes = store.getters.routes && store.getters.routes.length
      if (hasRoutes) {
        next()
      } else {
        const { userInfo } = store.getters
        const accessRoutes = await store.dispatch('permission/generateRoutes', userInfo.name)
        if (accessRoutes.length) router.addRoutes(accessRoutes)
        next({ ...to, replace: true })
      }
    }
  } else if (meta.requireAuth) {
    next(`/login?redirect=${path}`)
  }
})

router.afterEach(() => {
  console.log('afterEach')
})
