import "core-js/modules/es.object.to-string";
import "ant-design-vue/lib/message/style";
import _Message from "ant-design-vue/lib/message";
import axios from 'axios';
import store from '@/store';
var lock = true;
var axiosSource = axios.CancelToken.source(); // create an axios instance

var service = axios.create({
  baseURL: process.env.VUE_APP_BASEURL,
  // url = base url + request url
  withCredentials: false,
  // send cookies when cross-domain requests
  cancelToken: axiosSource.token // timeout: 5000 // request timeout

}); // axiosSource.cancel() // 调用这个方法关闭请求
// let loadingInstance  = null
// request interceptor

service.interceptors.request.use(function (config) {
  if (config.isLock) {
    if (!lock) return {};
    lock = false;
  } // do something before request is sent


  if (store.getters.token) {
    // let each request carry token
    // ['X-Token'] is a custom headers key
    // please modify it according to the actual situation
    // config.headers['X-Token'] = getToken()
    config.headers.token = store.getters.token;
  }

  return config;
}, function (error) {
  lock = true; // do something with request error

  console.log(error); // for debug

  return Promise.reject(error);
}); // response interceptor

service.interceptors.response.use(
/**
 * If you want to get http information such as headers or status
 * Please return  response => response
*/

/**
 * Determine the request status by custom code
 * Here is just an example
 * You can also judge the status by HTTP Status Code
 */
function (response) {
  lock = true;
  var res = response.data; // if the custom code is not 20000, it is judged as an error.

  if (res.code !== 200) {
    _Message.error(res.message || 'error');

    return Promise.reject(res.message || 'error');
  }

  return res;
}, function (error) {
  lock = true;
  var data = error.response && error.response.data;

  if (data) {
    var message = data.error && data.error.message; // error一般是网络问题

    _Message({
      content: message || 'error',
      top: '100px',
      duration: 3
    });
  }

  return Promise.reject(data);
});
export default service;