import request from '@/utils/request';
export function login(data) {
  return request({
    url: '/login',
    // 请求地址
    method: 'POST',
    // 请求类型
    data: data
  });
}
export function register(data) {
  return request({
    url: '/register',
    method: 'POST',
    data: data
  });
}
export function sms(data) {
  return request({
    url: '/sms',
    method: 'POST',
    data: data
  });
}